.ReactModal__Overlay {
	-webkit-perspective: 600;
	perspective: 600;
	opacity: 0;
	overflow-x: hidden;
	overflow-y: auto;
	background-color: rgba(0, 0, 0, .5);
	z-index: 101
}

.ReactModal__Overlay--after-open {
	opacity: 1;
	transition: opacity 150ms ease-out
}

.ReactModal__Content {
	-webkit-transform: scale(.5) rotateX(-30deg);
	transform: scale(.5) rotateX(-30deg)
}

.ReactModal__Content--after-open {
	-webkit-transform: scale(1) rotateX(0);
	transform: scale(1) rotateX(0);
	transition: all 150ms ease-in
}

.ReactModal__Overlay--before-close {
	opacity: 0
}

.ReactModal__Content--before-close {
	-webkit-transform: scale(.5) rotateX(30deg);
	transform: scale(.5) rotateX(30deg);
	transition: all 150ms ease-in
}

.ReactModal__Content.modal-dialog {
	border: none;
	background-color: transparent
}

.animated {
	animation-fill-mode: both
}

.animated.bounceIn,
.animated.bounceOut {
	animation-duration: .75s
}

.animated.shake {
	animation-duration: .3s
}

@keyframes shake {
	from,
	to {
		transform: translate3d(0, 0, 0)
	}
	10%,
	50%,
	90% {
		transform: translate3d(-10px, 0, 0)
	}
	30%,
	70% {
		transform: translate3d(10px, 0, 0)
	}
}

.shake {
	animation-name: shake
}

@keyframes bounceIn {
	20%,
	40%,
	60%,
	80%,
	from,
	to {
		animation-timing-function: cubic-bezier(.215, .61, .355, 1)
	}
	0% {
		opacity: 0;
		transform: scale3d(.3, .3, .3)
	}
	20% {
		transform: scale3d(1.1, 1.1, 1.1)
	}
	40% {
		transform: scale3d(.9, .9, .9)
	}
	60% {
		opacity: 1;
		transform: scale3d(1.03, 1.03, 1.03)
	}
	80% {
		transform: scale3d(.97, .97, .97)
	}
	to {
		opacity: 1;
		transform: scale3d(1, 1, 1)
	}
}

.bounceIn {
	animation-name: bounceIn
}

@keyframes bounceOut {
	20% {
		transform: scale3d(.9, .9, .9)
	}
	50%,
	55% {
		opacity: 1;
		transform: scale3d(1.1, 1.1, 1.1)
	}
	to {
		opacity: 0;
		transform: scale3d(.3, .3, .3)
	}
}

.bounceOut {
	animation-name: bounceOut
}

.s-alert-box,
.s-alert-box * {
	box-sizing: border-box
}

.s-alert-box {
	position: fixed;
	background: rgba(42, 45, 50, .85);
	padding: 22px;
	line-height: 1.4;
	z-index: 1000;
	pointer-events: none;
	color: rgba(250, 251, 255, .95);
	font-size: 100%;
	font-family: 'Helvetica Neue', 'Segoe UI', Helvetica, Arial, sans-serif;
	max-width: 300px;
	-webkit-transition: top .4s, bottom .4s;
	transition: top .4s, bottom .4s
}

.s-alert-box.s-alert-show,
.s-alert-box.s-alert-visible {
	pointer-events: auto
}

.s-alert-box a {
	color: inherit;
	opacity: .7;
	font-weight: 700
}

.s-alert-box a:focus,
.s-alert-box a:hover {
	opacity: 1
}

.s-alert-box p {
	margin: 0
}

.s-alert-close {
	width: 20px;
	height: 20px;
	position: absolute;
	right: 4px;
	top: 4px;
	overflow: hidden;
	text-indent: 100%;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden
}

.s-alert-close:focus,
.s-alert-close:hover {
	outline: 0
}

.s-alert-close::after,
.s-alert-close::before {
	content: '';
	position: absolute;
	width: 3px;
	height: 60%;
	top: 50%;
	left: 50%;
	background: #fff
}

.s-alert-close:hover::after,
.s-alert-close:hover::before {
	background: #fff
}

.s-alert-close::before {
	-webkit-transform: translate(-50%, -50%) rotate(45deg);
	transform: translate(-50%, -50%) rotate(45deg)
}

.s-alert-close::after {
	-webkit-transform: translate(-50%, -50%) rotate(-45deg);
	transform: translate(-50%, -50%) rotate(-45deg)
}

.s-alert-bottom-left {
	top: auto;
	right: auto;
	bottom: 30px;
	left: 30px
}

.s-alert-top-left {
	top: 30px;
	right: auto;
	bottom: auto;
	left: 30px
}

.s-alert-top-right {
	top: 30px;
	right: 30px;
	bottom: auto;
	left: auto
}

.s-alert-bottom-right {
	top: auto;
	right: 30px;
	bottom: 30px;
	left: auto
}

.s-alert-bottom,
.s-alert-top {
	width: 100%;
	max-width: 100%;
	left: 0;
	right: 0
}

.s-alert-bottom {
	bottom: 0;
	top: auto
}

.s-alert-top {
	top: 0;
	bottom: auto
}

.s-alert-info {
	background: #00A2D3;
	color: #fff
}

.s-alert-success {
	background: #27AE60;
	color: #fff
}

.s-alert-warning {
	background: #F1C40F;
	color: #fff
}

.s-alert-error {
	background: #E74C3C;
	color: #fff
}

[class*=" s-alert-effect-"].s-alert-hide,
[class^=s-alert-effect-].s-alert-hide {
	-webkit-animation-direction: reverse;
	animation-direction: reverse
}

.s-alert-box-height {
	visibility: hidden;
	position: fixed
}

.s-alert-effect-scale a,
.s-alert-effect-scale a:focus,
.s-alert-effect-scale a:hover {
	color: #fff
}

.s-alert-effect-scale .s-alert-close::after,
.s-alert-effect-scale .s-alert-close::before,
.s-alert-effect-scale .s-alert-close:hover::after,
.s-alert-effect-scale .s-alert-close:hover::before {
	background: #fff
}

.s-alert-effect-scale.s-alert-hide,
.s-alert-effect-scale.s-alert-show {
	-webkit-animation-name: animScale;
	animation-name: animScale;
	-webkit-animation-duration: .25s;
	animation-duration: .25s
}

@-webkit-keyframes animScale {
	0% {
		opacity: 0;
		-webkit-transform: translate3d(0, 40px, 0) scale3d(.1, .6, 1)
	}
	100% {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0) scale3d(1, 1, 1)
	}
}

@keyframes animScale {
	0% {
		opacity: 0;
		-webkit-transform: translate3d(0, 40px, 0) scale3d(.1, .6, 1);
		transform: translate3d(0, 40px, 0) scale3d(.1, .6, 1)
	}
	100% {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
		transform: translate3d(0, 0, 0) scale3d(1, 1, 1)
	}
}